.App-wrapper {
  margin-top: 7vh;
}

.App-calendar {
  height: 280px;
  width: 350px;
  box-shadow: -22px -10px 25px rgb(0 0 0 / 80%);
}

.App-calendar:after {
  top: -12px;
  left: -10px;
  height: 12px;
  width: 350px;
  transform: rotate(0deg) skewX(60deg);
}

.App-calendar:before {
  top: -6px;
  left: -20px;
  height: 280px;
  width: 20px;
  transform: rotate(0deg) skewY(30deg);
}

.App-content {
  box-shadow: -22px -10px 25px rgb(0 0 0 / 80%);
  z-index: -2;
}

.App-content:after {
  top: -12px;
  left: -10px;
  height: 12px;
  width: 100%;
  transform: rotate(0deg) skewX(60deg);
}

.App-content:before {
  top: -6px;
  left: -20px;
  height: 100%;
  width: 20px;
  transform: rotate(0deg) skewY(30deg);
  z-index: -1;
}

.App-content:after,
.App-content:before,
.App-calendar:after,
.App-calendar:before,
.item:after,
.item:before {
  content: "";
  position: absolute;
  background: #b1b1b1;
  box-shadow: inset 0px 0 1px rgb(0 0 0 / 30%);
}
.App-content:before {
  /* background: #6c6c6c; */
  box-shadow: inset 10px 0 10px rgb(0, 0, 0, 0.5);
}

.App-all-content {
  row-gap: 8vh;
}
